
const state = {
    chartOptionsHIS: {
        chart: {
            type: 'spline',
            animation: {
                duration: 1000
            }
        },
        title: {
            text: ''
        },
        xAxis:{
            title:{
                text:''
            }
        },
        yAxis: [{ // Primary yAxis
            labels: {
                format: '{value}C',
            },
            title: {
                text: '',
            },
            plotLines:[{
                color:'red',           //
                dashStyle:'solid',     //
                value:40,               //x3
                width:2                //2px
            }]
        }, { // Secondary yAxis
            title: {
                text: '',
            },
            labels: {
                format: '{value} %RH',
            },
            opposite: true
        }],
        legend: {
            layout: 'vertical',
            align: 'left',
            x: 120,
            verticalAlign: 'top',
            y: 100,
            floating: true,
        },
        series: [

        ],
        tooltip: {
            xDateFormat: '%Y-%m-%d',
            shared: true,
            crosshairs: true,
            animation: true,               // 
            style: {                      // 
                fontSize: "12px",
                fontWeight: "blod",
                fontFamily: "Courir new"
            }
        },
    },
    tempReal: 50,
    humiReal: 50
}

const mutations = {
    setTemHuiHis(state, TemHuiHis){
        state.chartOptionsHIS.series = TemHuiHis;
    },
    setTempReal(state, TempReal){
        state.tempReal =TempReal;
    },
    setHumiReal(state,HumiReal){
        state.humiReal = HumiReal;
    }
}

const getters = {

}


export default {
    namespaced: true,
    state,
    getters,
    mutations
}